import 'choices.js/public/assets/styles/choices.min.css';
import Choices from "choices.js";

document.addEventListener("livewire:init", function () {
    document.addEventListener("livewire:navigated", () => {
        const selects = document.querySelectorAll(".multiSelect");

        selects.forEach((selectbox) => {
            let select = new Choices(selectbox, {
                removeItemButton: true,
                addChoices: selectbox.getAttribute("data-create") === "true",

                duplicateItemsAllowed: false,
                paste: true,
                allowHTML: false,
                shouldSort: false,
                addItemText: (value) => `Press Enter to add: ${value}`,
            });

            // Retrieve preselected values
            let preselected = selectbox.getAttribute("data-selected");
            if (preselected) {
                let values = JSON.parse(preselected);
                select.setChoices(
                    values.map(value => ({ value, label: value, selected: true })),
                    "value",
                    "label",
                    true
                );
            }

            selectbox.addEventListener("change", function () {
                let selectedValues = selectbox.multiple
                    ? Array.from(this.selectedOptions).map(option => option.value)
                    : this.value;

                Livewire.dispatch("multiSelectUpdated", { key: selectbox.id, selected: selectedValues });
            });

            Livewire.on("reinitChoices", () => {
                select.destroy();
                select = new Choices(selectbox, {
                    removeItemButton: true,
                    addChoices: selectbox.getAttribute("data-create") === "true",
                    duplicateItemsAllowed: false,

                    paste: true,
                    allowHTML: false,
                    shouldSort: false,
                    addItemText: (value) => `Press Enter to add: ${value}`,
                });


                if (preselected) {
                    let values = JSON.parse(preselected);
                    select.setChoices(
                        values.map(value => ({ value, label: value, selected: true })),
                        "value",
                        "label",
                        true
                    );
                }
            });
        });
    });

});